<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Switch 开关</h2>
            <p slot="title" >表示两种相互对立的状态间的切换，多用于触发「开/关」。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <ShSwitch v-model="active" name="zs" activeColor="red"></ShSwitch>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>绑定v-model到一个Boolean类型的变量。可以使用active-color属性与inactive-color属性来设置开关的背景色。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;ShSwitch v-model=&quot;value&quot; name=&quot;zs&quot; activeColor=&quot;red&quot;&gt;&lt;/ShSwitch&gt;

&lt;script&gt;
  export default {
    data() {
      return {
        value: true
      }
    }
  };
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'SwitchOne',
  data(){
    return {
        isActive: false,
        active: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



