<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Image 图片</h2>
            <p slot="title" >图片容器，在保留原生img的特性下，支持懒加载，自定义占位、加载失败等。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <ShImage 
                        :src="src" 
                        fit="fill"
                        style="width: 40px; height: 40px"
                        ></ShImage>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>可通过fit确定图片如何适应到容器框，同原生 object-fit。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div class=&quot;demo-image&quot;&gt;
  &lt;div class=&quot;block&quot; v-for=&quot;fit in fits&quot; :key=&quot;fit&quot;&gt;
    &lt;span class=&quot;demonstration&quot;&gt;{{ fit }}&lt;/span&gt;
    &lt;ShImage
      style=&quot;width: 100px; height: 100px&quot;
      :src=&quot;url&quot;
      :fit=&quot;fit&quot;&gt;&lt;/ShImage&gt;
  &lt;/div&gt;
&lt;/div&gt;

&lt;script&gt;
  export default {
    data() {
      return {
        fits: [&#x27;fill&#x27;, &#x27;contain&#x27;, &#x27;cover&#x27;, &#x27;none&#x27;, &#x27;scale-down&#x27;],
        url: &#x27;https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg&#x27;
      }
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >大图预览</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <ShImage 
                        :src="src" 
                        fit="fill"
                        style="width: 40px; height: 40px"
                        preview=true
                        ></ShImage>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>可通过 preview 开启预览大图的功能。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div class=&quot;demo-image__preview&quot;&gt;
  &lt;ShImage
    style=&quot;width: 100px; height: 100px&quot;
    :src=&quot;url&quot; 
    preview=true;&gt;
  &lt;/ShImage&gt;
&lt;/div&gt;

&lt;script&gt;
  export default {
    data() {
      return {
        url: &#x27;https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg&#x27;
      }
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'ImageOne',
  data(){
    return {
        isActive: false,
        isActive1: false,
        src: '../static/img/shui.png',
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



