<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Button 按钮</h2>
            <p slot="title" >常用的操作按钮。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <p slot="title" >基础的按钮用法。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div class="row">
                            <ShButton>按钮1</ShButton>
                            <ShButton type="primary">按钮2</ShButton>
                            <ShButton type="success">按钮2</ShButton>
                            <ShButton icon="sh-icon-delete" type="info">按钮2</ShButton>
                            <ShButton type="warning">按钮2</ShButton>
                            <ShButton type="danger">按钮2</ShButton>
                            </div>
                            <div class="row">
                            <ShButton plain >按钮1</ShButton>
                            <ShButton plain type="primary">按钮2</ShButton>
                            <ShButton plain type="success">按钮2</ShButton>
                            <ShButton plain type="info">按钮2</ShButton>
                            <ShButton plain icon="sh-icon-delete" type="warning">按钮2</ShButton>
                            <ShButton plain type="danger">按钮2</ShButton>
                            </div>
                            <div class="row">
                            <ShButton plain round @click="fun">按钮1</ShButton>
                            <ShButton plain round type="primary">按钮2</ShButton>
                            <ShButton plain round type="success">按钮2</ShButton>
                            <ShButton plain icon="sh-icon-delete" round type="info">按钮2</ShButton>
                            <ShButton plain round type="warning">按钮2</ShButton>
                            <ShButton plain round type="danger">按钮2</ShButton>
                            </div>
                            <div class="row">
                            <ShButton plain circle >1</ShButton>
                            <ShButton plain circle type="primary">2</ShButton>
                            <ShButton plain circle type="success">2</ShButton>
                            <ShButton plain circle type="info">拆</ShButton>
                            <ShButton plain circle type="warning">2</ShButton>
                            <ShButton plain circle type="danger">2</ShButton>
                            </div>
                            <div class="row">
                            <ShButton plain icon="sh-icon-delete" disabled circle ></ShButton>
                            <ShButton plain icon="sh-icon-delete" circle type="primary"></ShButton>
                            <ShButton plain icon="sh-icon-delete" circle type="success">删除</ShButton>
                            <ShButton plain icon="sh-icon-delete" circle type="info"></ShButton>
                            <ShButton plain icon="sh-icon-delete" circle type="warning"></ShButton>
                            <ShButton plain icon="sh-icon-delete" circle type="danger"></ShButton>
                            </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>使用type、plain、round和circle属性来定义 Button 的样式。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div class=&quot;row&quot;&gt;
    &lt;ShButton&gt;按钮1&lt;/ShButton&gt;
    &lt;ShButton type=&quot;primary&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton type=&quot;success&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton icon=&quot;sh-icon-delete&quot; type=&quot;info&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton type=&quot;warning&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton type=&quot;danger&quot;&gt;按钮2&lt;/ShButton&gt;
&lt;/div&gt;
&lt;div class=&quot;row&quot;&gt;
    &lt;ShButton plain &gt;按钮1&lt;/ShButton&gt;
    &lt;ShButton plain type=&quot;primary&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain type=&quot;success&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain type=&quot;info&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; type=&quot;warning&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain type=&quot;danger&quot;&gt;按钮2&lt;/ShButton&gt;
&lt;/div&gt;
&lt;div class=&quot;row&quot;&gt;
    &lt;ShButton plain round @click=&quot;fun&quot;&gt;按钮1&lt;/ShButton&gt;
    &lt;ShButton plain round type=&quot;primary&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain round type=&quot;success&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; round type=&quot;info&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain round type=&quot;warning&quot;&gt;按钮2&lt;/ShButton&gt;
    &lt;ShButton plain round type=&quot;danger&quot;&gt;按钮2&lt;/ShButton&gt;
&lt;/div&gt;
&lt;div class=&quot;row&quot;&gt;
    &lt;ShButton plain circle &gt;1&lt;/ShButton&gt;
    &lt;ShButton plain circle type=&quot;primary&quot;&gt;2&lt;/ShButton&gt;
    &lt;ShButton plain circle type=&quot;success&quot;&gt;2&lt;/ShButton&gt;
    &lt;ShButton plain circle type=&quot;info&quot;&gt;拆&lt;/ShButton&gt;
    &lt;ShButton plain circle type=&quot;warning&quot;&gt;2&lt;/ShButton&gt;
    &lt;ShButton plain circle type=&quot;danger&quot;&gt;2&lt;/ShButton&gt;
&lt;/div&gt;
&lt;div class=&quot;row&quot;&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; disabled circle &gt;&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; circle type=&quot;primary&quot;&gt;&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; circle type=&quot;success&quot;&gt;删除&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; circle type=&quot;info&quot;&gt;&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; circle type=&quot;warning&quot;&gt;&lt;/ShButton&gt;
    &lt;ShButton plain icon=&quot;sh-icon-delete&quot; circle type=&quot;danger&quot;&gt;&lt;/ShButton&gt;
&lt;/div&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Button',
  data(){
    return {
        isActive: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



