<template>
  <div class="MianRigth">
      <Layoute v-if="this.$store.state.indexID == 12"></Layoute>
      <Container v-if="this.$store.state.indexID == 13"></Container>
      <Scrollbar v-if="this.$store.state.indexID == 77"></Scrollbar>
      <Color v-if="this.$store.state.indexID == 14"></Color>
      <Dialog v-if="this.$store.state.indexID == 63"></Dialog>
      <Button v-if="this.$store.state.indexID == 18"></Button>
      <ImageOne v-if="this.$store.state.indexID == 73"></ImageOne>
      <Input v-if="this.$store.state.indexID == 23"></Input>
      <SwitchOne v-if="this.$store.state.indexID == 27"></SwitchOne>
      <Color v-else ></Color>
  </div>
</template>

<script>
import Layoute from './tab/Layout'
import Container from './tab/Container'
import Color from './tab/Color'
import Scrollbar from './tab/Scrollbar'
import Dialog from './tab/Dialog'
import Button from './tab/Button'
import ImageOne from './tab/Image'
import Input from './tab/Input'
import SwitchOne from './tab/Switch'
export default {
  name: 'MianRigth',
  data(){
    return {
        id:0,
    }
  },
  components: {
      Layoute,
      Container,
      Color,
      Scrollbar,
      Dialog,
      Button,
      ImageOne,
      Input,
      SwitchOne
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
</style>



