<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Input 输入框</h2>
            <p slot="title" >通过鼠标或键盘输入字符
            Input 为受控组件，它总会显示 Vue 绑定值。

通常情况下，应当处理 input 事件，并更新组件的绑定值（或使用v-model）。否则，输入框内显示的值将不会改变。

不支持 v-model 修饰符。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <p slot="title" >基础的输入框用法。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div style="width: 200px;">
                            <Sh-Input v-model="input"></Sh-Input>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div style=&quot;width: 200px;&quot;&gt;
    &lt;Sh-Input v-model=&quot;input&quot; &gt;&lt;/Sh-Input&gt;
&lt;/div&gt;

&lt;script&gt;
  export default {
    data() {
      return {
        input: &#x27;&#x27;
      }
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >禁用状态</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div style="width: 200px;">
                            <Sh-Input v-model="input1" disabled></Sh-Input>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>通过 disabled 属性指定是否禁用 input 组件</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div style=&quot;width: 200px;&quot;&gt;
    &lt;Sh-Input v-model=&quot;input&quot; disabled=true &gt;&lt;/Sh-Input&gt;
&lt;/div&gt;

&lt;script&gt;
export default {
    data() {
        return {
            input: &#x27;&#x27;
        }
    }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >可清空</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div style="width: 200px;">
                            <Sh-Input v-model="input2" clearable></Sh-Input>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive2">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>使用clearable属性即可得到一个可清空的输入框</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div style=&quot;width: 200px;&quot;&gt;
    &lt;Sh-Input v-model=&quot;input&quot; clearable &gt;&lt;/Sh-Input&gt;
&lt;/div&gt;

&lt;script&gt;
export default {
    data() {
        return {
            input: &#x27;&#x27;
        }
    }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive2" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive2" class="sh-icon-caret-top"></i>
                        <span @click="isActive2 = !isActive2">{{ isActive2?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >密码框</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div style="width: 200px;">
                            <Sh-Input v-model="input3" show-password ></Sh-Input>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive3">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>使用show-password属性即可得到一个可切换显示隐藏的密码框</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div style=&quot;width: 200px;&quot;&gt;
    &lt;Sh-Input v-model=&quot;input&quot; show-password &gt;&lt;/Sh-Input&gt;
&lt;/div&gt;

&lt;script&gt;
export default {
    data() {
        return {
            input: &#x27;&#x27;
        }
    }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive3" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive3" class="sh-icon-caret-top"></i>
                        <span @click="isActive3 = !isActive3">{{ isActive3?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Input',
  data(){
    return {
        isActive: false,
        isActive1: false,
        isActive2: false,
        isActive3: false,
        input: '',
        input1: '',
        input2: '',
        input3: '',
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



