<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Dialog 对话框</h2>
            <p slot="title" >在保留当前页面状态的情况下，告知用户并承载相关操作。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基本用法</h3>
            <p slot="title" >Dialog 弹出一个对话框，适合需要定制性更大的场景。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-button type="text" @click="dialogVisible = true">点击打开 Dialog</sh-button>
                        <sh-dialog
                        title="提示"
                        :visible.sync="dialogVisible"
                        width="30%">
                            <span>这是一段信息</span>
                            <span slot="footer" class="dialog-footer">
                                <sh-button @click="dialogVisible = false">取 消</sh-button>
                                <sh-button type="primary" @click="dialogVisible = false">确 定</sh-button>
                            </span>
                        </sh-dialog>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>需要设置visible属性，它接收Boolean，当为true时显示 Dialog。Dialog 分为两个部分：body和footer，footer需要具名为footer的slot。title属性用于定义标题，它是可选的，默认值为空。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;ShButton type=&quot;primary&quot; @click=&quot;visible = true&quot;&gt;按钮&lt;/ShButton&gt;
      &lt;ShDialog width=&quot;20%&quot; top=&quot;200px&quot; :visible.sync=&quot;visible&quot;&gt;
        &lt;ul&gt;
          &lt;li&gt;1&lt;/li&gt;
          &lt;li&gt;1&lt;/li&gt;
          &lt;li&gt;1&lt;/li&gt;
        &lt;/ul&gt;
        &lt;template v-slot:footer&gt;
          &lt;ShButton type=&quot;primary&quot; @click=&quot;visible = false&quot;&gt;确定&lt;/ShButton&gt;
          &lt;ShButton @click=&quot;visible = false&quot;&gt;取消&lt;/ShButton&gt;
        &lt;/template&gt;
      &lt;/ShDialog&gt;
&lt;script&gt;

export default {
  name: &#x27;App&#x27;,
  data(){
    return {
      visible: false
    }
  },
  methods: {
  }
}
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Dialog',
  data(){
    return {
        isActive: false,
        dialogVisible: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



